<template>
    <div class="map-large">
        <div class="day-map-large">
            <svg xmlns="http://www.w3.org/2000/svg" version="1.2" baseProfile="tiny" width="440" height="550"
                 viewBox="0 0 440 550" stroke-linecap="round" stroke-linejoin="round">
                <g id="finland-large">
                    <path v-for="path in paths" :key="path.key" stroke="#000000" stroke-width="0.5"
                          :fill="path.fill" :d="path.d" :opacity="path.opacity" />
                </g>
            </svg>
            <b-button id="fmi-warnings-zoom-in" class="fmi-warnings-zoom"></b-button>
            <b-button id="fmi-warnings-zoom-out" class="fmi-warnings-zoom"></b-button>
        </div>
    </div>
</template>

<script>
import i18n from '../i18n';
import config from '../mixins/config';
import geometry from '../mixins/geometry';
import utils from '../mixins/utils';

export default {
  name: 'MapLarge',
  props: {
    index: {
      type: Number,
    },
    input: {
      type: Object,
      default: () => ({}),
    },
  },
  mixins: [config, geometry, utils],
  computed: {
    warnings() {
      return i18n.t('warnings');
    },
    paths() {
      return this.regionIds.map((regionId) => {
        const regionGeom = this.geometries[regionId];
        const regionColor = this.regionColor(regionId);
        const visible = ((regionGeom.subType !== this.REGION_LAKE) || (regionColor !== this.colors.sea));
        return {
          key: `large-${regionId}`,
          fill: regionColor,
          d: regionGeom.pathLarge,
          opacity: visible ? '1' : '0',
        };
      });
    },
  },

  data: () => ({
    warningsDate: '',
    updated: '',
    updatedDate: '',
    atTime: '',
    updatedTime: '',
    dataProviderFirst: '',
    dataProviderSecond: '',
  }),
};
</script>

<style scoped lang="scss">
    @import "../scss/constants.scss";

    div.map-large {
        display: inline-block;
        width: $map-large-width;
        height: $map-large-height;
        background-color: rgba(0, 0, 0, 0);
    }

    button.fmi-warnings-zoom {
        position: absolute;
        right: 10px;
        border-color: #53b9e6;
        height: 35px;
